/*-------------------------------------------------------------------
2. Font Awesome fixes for Gutenberg
--------------------------------------------------------------------*/
.editor-styles-wrapper svg.svg-inline--fa {
  vertical-align: -0.125rem;
}

/*-------------------------------------------------------------------
3. UDS Grid Links, Admin
--------------------------------------------------------------------*/
.acf-block-preview .uds-grid-links a {
  pointer-events: none;
}

.card {
  max-width: none;
}

.acf-block-preview .uds-tabbed-panels a.scroll-control-prev,
.acf-block-preview .uds-tabbed-panels a.scroll-control-next {
  display: none;
}

/*-------------------------------------------------------------------
4. UDS Profile Block, Admin
--------------------------------------------------------------------*/
.acf-block-preview .profile.profile-type-standard .profile-row {
  border-bottom: 2px #e0e0e0 solid;
  display: flex;
  padding-bottom: 32px;
  padding-top: 32px;
  pointer-events: none;
}
.acf-block-preview .profile.profile-type-standard .profile-row .profile-photo-column {
  padding: 24px 24px 24px 0;
}
.acf-block-preview .profile.profile-type-standard .profile-row .profile-photo-column img.pictureOriginal {
  width: 180px;
  height: 180px;
  border-radius: 500px;
  margin-top: 0;
  object-fit: cover;
}
.acf-block-preview .profile.profile-type-standard .profile-row .no-bottom-border {
  border-bottom: none !important;
}
.acf-block-preview .profile.profile-type-standard .profile-row .profile-bio-column {
  max-width: 700px;
}
.acf-block-preview .profile.profile-type-standard .profile-row .profile-bio-column .profile-contact-row {
  display: flex;
}
.acf-block-preview .profile.profile-type-standard .profile-row .profile-bio-column .profile-contact-row > div {
  margin-right: 32px;
}
.acf-block-preview .profile.profile-type-standard .profile-row .profile-bio-column .profile-name {
  margin-top: 24px;
}
.acf-block-preview .profile.profile-type-standard .profile-row .profile-bio-column .profile-name a {
  color: #000;
  text-decoration: none;
}
.acf-block-preview .profile.profile-type-standard .profile-row .profile-bio-column .profile-name a:hover {
  color: blue !important;
}
.acf-block-preview .profile.profile-type-standard .profile-row .profile-bio-column .profile-name .titleOriginal {
  font-weight: bold;
}